* {
  box-sizing: border-box;
}

.App {
  width: 90vw;
  margin: 0 auto;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
}

.search-bar {
  background: url("images/Search.svg") no-repeat 25px 23px;

  border: 1px solid #e1e7ed;
  box-sizing: border-box;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
  width: 100%;

  color: #3d4e61;
  font-family: Avenir, Arial, serif;
  font-weight: normal;

  padding-left: 62px;
  margin-bottom: 15px;
  height: 70px;
  font-size: 16px;
  line-height: 22px;
}

.search-bar:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2) ;
}

.search-bar:focus {
  border-radius: 3px;
  outline: none;
  font-weight: bold;

  background: url("images/Search.svg") no-repeat 25px 23px, linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  background-size: 19px;
}
